body,html {
  margin: 0;
  padding: 0;
  font-family: Consolas,"courier new";
  overflow-y: hidden;
}

.entire-container {
  display: flex;
  flex-direction: row;
  height: 100vh;
}

.cinema {
  height: 60vh;
  border: 5vh solid #0b0704;
  display: flex;
  /*background: linear-gradient(to bottom right, #b64d39 , #6b3d2a);*/
  background-image: repeating-radial-gradient(circle at 17% 32%, white, black 0.00085px);
  position: relative;
}

.animation1 {
  animation: back1 3s linear infinite;
}

.animation2 {
  animation: back2 1s linear infinite;
}

@keyframes back1 {
  from {
    background-size: 100% 100%;
  }

  to {
    background-size: 99% 100%;
  }
}

@keyframes back2 {
  from {
    background-size: 48.56% 50%;
  }

  to {
    background-size: 43.9% 50.1%;
  }
}

.clip {
  height: 60vh;
  width: 100%;
  object-fit: fill;
}

.mouth {
  width: 6vw;
  position: absolute;
  top: 64%;
  right: 40%;
  border-radius: 50px;
}

.seats {
  width: 100%;
  height: 30vh;
  background-image:  url('../images/cinema.jpg');
  background-size: cover;
  position: relative;
}

.lighted-seats {
  width: 100%;
  height: 30vh;
  background-image:  linear-gradient(to bottom, grey, rgba(0, 0, 0, 0.5)), url('../images/cinema.jpg');
  background-size: cover;
  position: relative;
}


.left-side, .right-side {
  flex: 1;
}

.nav-bar {
  background-color: #166cee;
  height: 3.5vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 5px solid #c5c5c0;
}

.logo {
  height: 20px;
  width: 25px;
}

.nav-bar p {
  color: #c5c5c0;
  font-weight: bold;
  margin: 5px 5px;
}

.left-nav {
  display: flex;
  align-items: center;
}

.main-right {
  height: 100vh;
  background-image: url("../images/windowsxp.jpg");
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.taskbar {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 4vh;
  background-color: #235cda;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.start {
  background-color: #43aa44;
  color: #f3eded;
  display: flex;
  justify-content: center;
  align-items: center;
  font-style: italic;
  width: 5.3vw;
  text-align: center;
  border-radius: 2px 5px 5px 2px;
  font-weight: bold;
  font-size: large;
}

.windowsxplogo {
  height: 2vh;
  width: 2vh;
  padding-right: 2px;
}

.right-half-taskbar {
  width: 44.3vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cmd-taskbar {
  width: 10vw;
  background-color: #1a51bd;
  outline: 2px solid #19419f;
  border-radius: 5px;
  height: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: flex;
  align-items: center;
  color: #c5c5c0;
}

.time {
  background-color: #12a8fc;
  width: 5vw;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f3eded;
  border-radius: 5px 2px 2px 5px;
  height: 100%;
}

.recycle {
  height: 45px;
  width: 70px;
  position: absolute;
  top: 5vh;
  left: 1vw;
}

.caption {
  position: absolute;
  top: 10vh;
  left: 1vw;
  font-size: smaller;
  color: white;
  text-shadow: 1px 1px 3px #0b0704;
}

.terminal {
  height: 55vh;
  width: 40vw;
  background-color: black;
  border: 5px solid #e6e0cf;
}

.windows-controls {
  display: flex;
  align-items: center;
}

.windows-controls img {
  height: 30px;
  width: 22px;  
  opacity: 60%;
}

.command-prompt {
  padding: 0px 5px;
  height: 51vh;
  color: #c0c0c0;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}

.command-prompt ::selection {
  background-color: #c0c0c0;
}
.command-prompt ::-webkit-scrollbar-thumb {
  background: #ecead8;
}

.current-line {
  display: flex;
  justify-content: space-between;
}

.current-line b {
  font-weight: bold;
  font-size: initial;
}

.current-line input {
  border: none; 
  background: transparent; 
  outline: 0;
  color: #c0c0c0;
  font-weight: bold;
  font-family: Consolas,"courier new";
  font-size: initial;
  caret-color: transparent;
}

.placeholder {
  font-weight: bold;
  animation-name: blink;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}


@keyframes blink {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.text-container {
  display: flex;
  flex: 1;
}
